import React, { Component } from "react";
import ReactPlayer from "react-player";
import Modal from "react-responsive-modal";

class Player extends Component {
  render() {
    const { open, toggleModal } = this.props;
    return (
      <Modal
        open={open}
        onClose={toggleModal}
        styles={{
          modal: {
            maxWidth: "unset",
            width: "100%",
            padding: "unset",
            background: "black"
          },
          overlay: {
            background: "rgba(0, 0, 0, 0.5)"
          },
          closeButton: {
            background: "rgba(255,255, 255, 0.5)"
          }
        }}
        center
      >
        <ReactPlayer
          url="https://res.cloudinary.com/wikacy-com/video/upload/f_auto,q_auto:good,so_148/v1668388296/promos/magic-books_promo-my-deliverer_ip4jjg.mp4"
          width="100%"
          height="calc(100vh - 100px)"
          controls={false}
          playing={true}
        />
      </Modal>
    );
  }
}

export default Player;
